.footer-item>a svg,
.footer-item>a i {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    font-size: 30px;
}

.footer-item a {
    display: flex;
    align-items: center;
    gap: 10px;
}

.footer-item .d-flex {
    margin-top: 20px;
    gap: 10px;

}

.footer-item .d-flex .socialShare.facebook {
    background-color: rgba(17, 150, 245, 0.7);
}

.footer-item .d-flex .socialShare.instagram {
    background: linear-gradient(rgba(114, 80, 200, 0.7), rgba(212, 41, 115, 0.7), rgba(244, 90, 52, 0.7), rgba(253, 189, 87, 0.7));
}

.footer-item .d-flex .socialShare.youtube {
    background-color: rgba(253, 57, 50, 0.7);
}

.footer-item .d-flex .socialShare.facebook:hover {
    background-color: rgb(17 149 245);
}

.footer-item .d-flex .socialShare.instagram:hover {
    background: linear-gradient(rgb(114 80 200), rgb(212 41 114), rgb(244 90 52), rgb(253 189 87));
}

.footer-item .d-flex .socialShare.youtube:hover {
    background-color: rgb(253 56 50);
}

.footer-item .d-flex .socialShare {
    height: 30px;
    width: 30px;
    background-color: rgb(70, 66, 66);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-item .d-flex .socialShare img,
.footer-item .d-flex .socialShare svg {
    height: 22px;
    width: 22px;
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3px;
    padding:40px 15px;
    border-radius: 10px;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    background-color: rgba(255, 255, 255, 0.062);
}
.footer-item.Main{
    gap: 10px;
}
.logo .description {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3px;
    flex-direction: column;

}

.logo .description .title {
    text-transform: uppercase;
    font-size: 25px;
    font-weight: 800;
}

.logo .description .subtitle {
    text-transform: capitalize;
    font-size: 17px;
    letter-spacing: 1px;
    /* font-weight: 800; */
}

.logo .image img {
    height: 80px;
    width: 120px;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    border: 2px solid white;
}

.footer-item.Main .about{
    color: white;
    font-size: 18px;
}