.logo .title {}

.topbar {
    padding: 0px 5px;
    background: rgb(168, 159, 159);
}

.topbar .left img,
.topbar .left svg {
    height: 30px;
    width: 30px;
    border-radius: 50%;
}

.topbar .right img,
.topbar .right svg {
    height: 22px;
    width: 22px;
}

.topbar .left a.phone-call svg {
    height: 20px !important;
    width: 20px !important;
}

.topbar .left a.phone-call {
    height: 30px;
    width: 30px;
    background-color: rgb(70, 67, 67);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.topbar .main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    padding: 0px 20%;
}

.wrapToggle {
    gap: 10px !important;
}

@media (width<300px) {
    .topbar .main {
        padding: 0px;
    }
}

.topbar .main .left,
.topbar .main .right {
    gap: 0.5em;
    justify-content: end;
}

.topbar .main .socialShare.facebook {
    background-color: rgba(17, 150, 245, 0.7);
}

.topbar .main .socialShare.instagram {
    background: linear-gradient(rgba(114, 80, 200, 0.7), rgba(212, 41, 115, 0.7), rgba(244, 90, 52, 0.7), rgba(253, 189, 87, 0.7));
}

.topbar .main .socialShare.youtube {
    background-color: rgba(253, 57, 50, 0.7);
}

.topbar .main .socialShare.facebook:hover {
    background-color: rgb(17 149 245);
}

.topbar .main .socialShare.instagram:hover {
    background: linear-gradient(rgb(114 80 200), rgb(212 41 114), rgb(244 90 52), rgb(253 189 87));
}

.topbar .main .socialShare.youtube:hover {
    background-color: rgb(253 56 50);
}

.topbar .main .socialShare {
    height: 30px;
    width: 30px;
    background-color: rgb(70, 66, 66);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.navigationbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 80px;
    position: relative;
}

.navigationbar .rightmost {
    display: flex;
    gap: 40px;
    height: 100%;
    margin: 0px;
    padding: 0px;
    align-items: center;
    justify-content: center;
}

.navigationbar .navigationbar-brand {
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigationbar .navigationbar-brand img {
    height: 60px;
    width: 100px;
}

.navigationbar-nav {
    gap: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigationbar-nav .nav-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: black;
    padding: 10px;
    font-weight: 800;
    font-size: 17px;
    gap: 3px;
}

.navigationbar-nav .nav-list.active,
.navigationbar-nav .nav-list:hover {
    background-color: rgb(255, 0, 157);
    color: blue;
}

.navigationbar-nav .nav-list:hover svg {
    fill: blue !important;
    stroke: blue !important;
}

.navigationbar-nav .nav-list.active:hover svg,
.navigationbar-nav .nav-list.active svg {
    fill: rgb(255, 255, 255) !important;
    stroke: rgb(255, 255, 255) !important;
}

.navigationbar-nav .nav-list.active,
.navigationbar-nav .nav-list.active:hover {
    color: white;
}

.navigationbar-nav .nav-list svg {
    height: 20px;
    width: 20px;
}

.navigationbar-nav .nav-list-dd.dropdown-block {
    position: relative;
}

.navigationbar-nav .nav-list-dd.dropdown-block .menu-dropdown {
    display: none;
}

.navigationbar-nav .nav-list-dd.dropdown-block:hover .menu-dropdown {
    display: block!important;
    position: absolute;
    background-color: white;
    width: 220px;
    box-shadow: 0px 5px 5px 0px rgb(56, 51, 51);
}

.navigationbar-nav .nav-list-dd.dropdown-block .menu-dropdown .nav-list {
    justify-content: left;
    padding-left: 20px;
    padding-right: 20px;
}

.navigationbar-nav hr {
    height: 1px;
    padding: 0px;
    margin: 0px;
    background-color: rgb(78, 78, 78);
    border: none;
}

.navigationbar-nav .nav-list-dd .arrow-last {
    display: none;
}

.navigationbar-toggler {
    display: none;
    border: none;
    outline: 0;
    background-color: transparent;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
}

.wrapToggle {
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.navigationbar-toggler svg {
    height: 100%;
    width: 100%;
}

@media (max-width:1000px) {
    

    .navigationbar-nav {
        gap: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .navigationbar-nav .nav-list svg {
        height: 15px;
        width: 15px;
    }

    .navigationbar-nav .nav-list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: black;
        padding: 5px;
        font-weight: 800;
        font-size: 14px;
        gap: 3px;
    }
}
@media (max-width:800px) {
    .navigationbar {
        padding: 0 40px;
        padding-right: 10px;
    }

}
@media (max-width: 700px) {
    .navigationbar-nav {
        position: absolute !important;
        display: block;
        margin-top: 25px;
        background-color: white;
        width: 100%;
        color: black;
        left: 0px;
        box-shadow: 0px 5px 5px 0px rgb(56, 51, 51);
    }

    .navigationbar-collapse.collapsed {
        display: none;
    }

    .navigationbar-toggler {
        display: flex;
    }

    .navigationbar-nav .nav-list-dd.dropdown-block:hover .menu-dropdown {
        width: 100%;
        position: relative;
        box-shadow: none;
    }

    .navigationbar-nav .nav-list-dd .arrow-first {
        display: none;
    }

    .navigationbar-nav .nav-list-dd .arrow-last {
        display: block;
    }

    .navigationbar-nav .nav-list-dd.dropdown-block .menu-dropdown {
        display: block;
    }

    .navigationbar-nav .nav-list-dd.dropdown-block .menu-dropdown.collapsed {
        display: none!important;
    }
}

iframe#webpack-dev-server-client-overlay {
    display: none !important;
}

.navigationbar .getQuote {
    padding: 5px 10px;
    font-size: 20px;
    font-weight: 800;
    color: white;
    background-color: rgba(233, 108, 7, 0.836);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.navigationbar .getQuote:hover {
    background: linear-gradient(45deg, blue, rgb(255, 0, 200), green);
}

@media (max-width:600px) {
    .navigationbar .navigationbar-brand img {
        height: 60px;
        width: 100px;
    }

    .navigationbar-nav .nav-list svg {
        height: 20px;
        width: 20px;
    }

    .navigationbar-toggler {

        height: 40px;
        width: 40px;
    }

    .navigationbar .getQuote {
        font-size: 15px;
        padding: 3px 6px;
    }

    .navigationbar {
        padding: 0px 15px;
    }
}

@media (max-width:300px) {
    .navigationbar .navigationbar-brand img {
        height: 40px;
        width: 60px;
    }

    .navigationbar-nav .nav-list svg {
        height: 15px;
        width: 15px;
    }

    .navigationbar-toggler {

        height: 40px;
        width: 40px;
    }

    .navigationbar .getQuote {
        font-size: 12px;
        padding: 3px 6px;
    }

    .navigationbar {
        padding: 0px 2px;
    }
}