.modal-content {
    border-radius: 18px;
    backdrop-filter: blur(15px);
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.25);
    animation: zoomIn 0.35s ease;
}

@keyframes zoomIn {
    from {
        transform: scale(0.85);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

/* Form input icons */
.input-group-text {
    background: white;
    border-right: none;
}

.form-control {
    border-left: none;
}
.modal-body input.form-control:focus,.modal-body input.form-control:hover,.modal-body input.form-control:active, .modal-body select.form-select:focus,.modal-body select.form-select:hover,.modal-body select.form-select:active,.modal-body textarea.form-control:focus,.modal-body textarea.form-control:hover,.modal-body textarea.form-control:active{
    outline: 0!important;
    border: 1px solid #ced4da!important;
    box-shadow: none!important;
    border-left: none!important;
}
.modal-body .input-group:has(input:focus),.modal-body .input-group:has(select:focus),.modal-body .input-group:has(textarea:focus){
    border: 1px solid blue;
    border-radius: 10px;
}
.company-logo {
    width: 130px;
    height: 70px;
    object-fit: contain;
    border-radius: 12px;
    margin-bottom: 10px;
}

.modal-header {
    border: none;
    text-align: center;
    width: 100%;
    display: block;
    padding-bottom: 0;
}

.modal-footer {
    border: none;
}

.book-btn {
    background: #0044ff;
    color: white;
    border-radius: 10px;
    padding: 12px 20px;
    font-size: 18px;
    border: none;
    box-shadow: 0 4px 14px rgba(0, 0, 0, 0.2);
}